.infos-bar{
    padding: 16px;
    background-color: #fff;
    display: flex;
    justify-content: space-between;

    &__infos{
        display: flex;
        gap: 40px;

        &__item{
            font-size: 14px;
            color:#000;

            b {
                color:#000;
                font-size: 18px;
                display: block;
            }
        }
    }

    @media (max-width: 640px){
        display: block;


        &__infos{
            display: block;

            &__intem{
                margin-bottom: 24px;
            }
        }
    }
}